.quote {
  font-family: HelveticaNoue37ThinOblique;
  color: #cebe98;
  letter-spacing: 1px;
  line-height: 1.4em;
  z-index: 2; }

.quote:before {
  content: "“";
  font-family: HelveticaNoue;
  color: #f1e7d2;
  font-weight: 600;
  font-size: 120%; }

.quote:after {
