/*--------------------------------------------------------------------------------------------------*/
/* R E S E T  B R O W S E R  S T Y L E: resetear los inherit styles del browser (si es que los tiene) */
* {
  margin: 0;
  padding: 0;
  border: 0; }

/*--------------------------------------------------------------------------------------------------*/
/* A L T U R A  D E  L A  N A V B A R  (escribir aquí la altura que queremos que tenga) */
/*---esto se cambia automáticamente acorde a la altura ingresada de la Navbar
para que el contenido comience luego de la Navbar y no se pierda nada detrás de ella---*/
body {
  padding-top: 140px;
  background-color: white; }

@media (max-width: 767px) {
  body {
    padding-top: 68px; } }

/* escribir aquí la altura de las columnas del footer, a criterio según la cantidad de info que tengan */
/*--------------------------------------------------------------------------------------------------*/
/* B O D Y   W R A P P E R */
.global-wrapper {
  display: block;
  overflow-x: hidden;
  padding-top: 140px;
  margin-top: -140px;
  background-color: white; }

@media (max-width: 767px) {
  .global-wrapper {
    padding-top: 68px;
    margin-top: -68px; } }

/*--------------------------------------------------------------------------------------------------*/
/* S E C C I O N E S  d e  C A D A  P Á G I N A */
/* S E C T I O N S   # I D s */
/*---escribir aquí las #id de las secciones para que el desplazamiento de los links hacia las secciones sean precisas---*/
#section0,
#section1 {
  overflow: hidden; }

/* S E C T I O N   W R A P P E R S */
/*escribir aquí el padding (superior e inferior) de cada sección dentro de una pág*/
/*---WRAPPER para envolver cada sección dentro de cada página; esta clase se le adjudica
al container-fluid que adentro contiene los rows y las columns que agregan los márgenes laterales---*/
.section-wrapper {
  /*envolvente de cada sección de cada página*/
  padding-top: 40px;
  padding-bottom: 40px; }

@media (min-width: 992px) {
  .section-wrapper-footer {
    /*envolvente sólo para el footer, con menor padding abajo*/
    padding-top: 40px;
    padding-bottom: 20px; } }

/*---para agregarle espaciado entre rows o items acorde a mi criterio (¡no aplicar a sections, porque ya tienen padding)---*/
.space-top {
  padding-top: 40px; }

.space-bottom {
  padding-bottom: 40px; }

.super-space-top {
  padding-top: 80px; }

.super-space-bottom {
  padding-bottom: 80px; }

.half-space-bottom {
  padding-bottom: 20px; }

.half-space-top {
  padding-top: 20px; }

/*---particulares de estas secciones (porque el contenido tiene posición absoluta y es necesario esto)---*/
#section0 > div {
  border-bottom: 5px solid #cebe98; }

#section3 > .container-fluid {
  min-height: 800px; }

#section4 > .container-fluid {
  padding-top: 3px; }

