/* I M A G E N   D E   P O R T A D A  de altura acorde al viewport */
div.wrapper-portada {
  height: 70vh;
  width: 100%;
  text-align: center;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  -moz-align-items: center;
  align-items: center;
  -webkit-box-pack: row;
  -ms-flex-pack: row;
  -webkit-justify-content: row;
  -moz-justify-content: row;
  justify-content: row; }

div.wrapper-portada > img.portada {
  max-height: 100%;
