/*  B O T Ó N   A M P L I A R
(para todos esos links de "ver más") con triángulo */
.ampliar {
  color: #917e4b;
  padding-left: 0px;
  -webkit-transition: all .3s ease;
  -moz-transition: all .3s ease;
  transition: all .3s ease; }

.ampliar span.glyphicon {
  color: #f1e7d2;
  -webkit-animation-name: mibounceleft;
  -webkit-animation-duration: 0.5s;
  -webkit-animation-play-state: paused;
  -webkit-animation-fill-mode: both;
  -webkit-animation-iteration-count: infinite;
  -webkit-animation-fill-mode: none;
  animation-name: mibounceleft;
  animation-duration: 0.5s;
  animation-play-state: paused;
  animation-fill-mode: both;
  animation-iteration-count: infinite;
  animation-fill-mode: none; }

.ampliar:hover {
  color: #f1e7d2;
  padding-left: 5px; }

.ampliar:hover span.glyphicon {
  -webkit-animation-play-state: running;
  animation-play-state: running; }

@-webkit-keyframes mibounceleft {
  0% {
    -webkit-transform: translate(0, 0); }
  50% {
    -webkit-transform: translate(-8px, 0);
    -webkit-animation-timing-function: ease-out; }
  100% {
    -webkit-transform: translate(0, 0);
    -webkit-animation-timing-function: ease-in; } }

@keyframes mibounceleft {
  0% {
    transform: translate(0, 0); }
  50% {
    transform: translate(-6px, 0);
    animation-timing-function: ease-out; }
  100% {
    transform: translate(0, 0);
    animation-timing-function: ease-in; } }

