/*escribir aquí la fuente utilizada para el logo de la empresa*/
/* E S T I L O   D E   T E X T O   N O R M A L */
p {
  font-family: HelveticaNoue;
  font-size: 14px;
  color: #080808;
  text-align: justify;
  white-space: pre-wrap; }

h1 {
  /*usar para título único por página*/
  color: #917e4b;
  font-family: HelveticaNoue37Thin; }

h2 {
  /*usar para títulos de las dif. secciones de cada página*/
  color: #917e4b;
  font-family: HelveticaNoue37Thin; }

h3 {
  /*usar para títulos de cada bloque de párrafo*/
  color: #4e4e4e;
  font-family: HelveticaNoue37Thin;
  letter-spacing: 1px; }

h4 {
  /*usar para párrafos cortos de alta relevancia*/
  color: #080808;
  font-family: HelveticaNoue37Thin;
  letter-spacing: 1px; }

h5 {
  /*usar para items o nombres puntuales que no son textos largos*/
  color: #080808;
  font-family: HelveticaNoue; }

h6 {
  /*usar para algún texto especial, de tamaño menor que los p*/
  color: #080808;
  font-family: HelveticaNoue; }

/*para que el cursor no se transforme en selector de texto*/
p, h1, h2, h3, h4, h5, h6 {
  cursor: default;
  text-decoration: none;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* letter-spacing:1px; */ }

/* E S T I L I Z A N D O   C O M P O R T A M I E N T O S   D E   H I P E R V Í N C U L O S */
/*para que el cursor se transforme en el dedito al pasar sobre links
y para quitar el subrayado de los links*/
a, a p, a h1, a h2, a h3, a h4, a h5, a h6 {
  cursor: pointer;
  cursor: hand;
  text-decoration: none;
  outline: none; }

a, a > p {
  text-decoration: none;
  outline: none;
  color: #cebe98; }

a:hover, a:focus, a > p:hover, a > p:focus {
  text-decoration: none;
  outline: none;
  color: #decdab; }

/* R E S P O N S I V E - @media settings */
@media (min-width: 450px) and (max-width: 767px) {
  p {
    font-size: 12px; }
  h1 {
    font-size: 28px; }
  h2 {
    font-size: 24px; }
  h3 {
    font-size: 19px; }
  h4 {
    font-size: 15px; }
  h5 {
    font-size: 14px; }
  h6 {
    font-size: 12px; } }

@media (max-width: 449px) {
