/* ------------------------------------------------------------------------------------------------*/
/* ------------------------------------------------------------------*/
/* * * * *    V  I  S  U  A  L     E  F  F  E  C  T  S    * * * * */
/* * * * * * *  algunos FX para meterle onda al texto  * * * * * * */
/* * * * *   E F E C T O   D E   3D (texto con volúmen)   * * * * */
.text-fx-3d {
  text-shadow: 0 1px 0 #ccc, 0 2px 0 #c9c9c9, 0 3px 0 #bbb, 0 4px 0 #b9b9b9, 0 5px 0 #aaa, 0 6px 1px rgba(0, 0, 0, 0.1), 0 0 5px rgba(0, 0, 0, 0.1), 0 1px 3px rgba(0, 0, 0, 0.3), 0 3px 5px rgba(0, 0, 0, 0.2), 0 5px 10px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.2), 0 20px 20px rgba(0, 0, 0, 0.15); }

/* * * * *   E F E C T O   D E   P R O F U N D I D A D (hundido sobre el background)   * * * * */
.text-fx-inset {
  background-color: #666666;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-clip: text;
  color: transparent;
  text-shadow: rgba(255, 255, 255, 0.3) 0px 3px 3px, rgba(0, 0, 0, 0.2) 0px -3px 2px; }

/* * * * *   E F E C T O   D E   S O M B R A  (simple)   * * * * */
.text-fx-shadow {
  text-shadow: rgba(0, 0, 0, 0.2) 0px 3px 2px; }

